img {
    max-width: 100%;
    height: auto;
}

img.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.img-fluid {
    max-width: 100%;
    height: auto
}

.img-gallery-paragraph {
    max-width: 1000px;
    width: 70%;
    text-align: justify;
    text-justify: inter-word;
    margin-right: 10px;
}

.img-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, 150px);
    grid-auto-rows: 150px;
    grid-gap: 1px;
    margin-left: 10px;
    margin-bottom: 25px;
}

.rounded_corners {
    border-radius: 10px;
}

figure {
    padding: 0;
    margin-left: 0;
    margin-top: 0;
    margin-bottom: 50px;
    margin-right: 25px;
}

figcaption {
    color: #222;
    margin: 5px;
    font-size: 16px;
}

.figure_standard {
    max-width: 350px;
    float: left;
}

.figure_large {
    max-width: 600px;
    max-height: 450px;
}

.banner {
    padding-bottom: 20px;
    margin-left: 0;
    margin-right: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    border: solid 0 black;
    overflow: hidden;
}

.picture_frame {
    border: 0 solid #000000;
    padding-top: 0px;
    padding-right: 5px;
    padding-left: 0;
    padding-bottom: 0;
}

.frame_left {
    border: 2px solid #CCCCCC;
    padding: 2px;
    margin-right: 5px
}

.jumbo img {
    width: 100%;
    height: auto;
    margin-left: 0;
    grid-row-start: 3;
    grid-row-end: 4;
}

.jumboimg img {
    width: 100%;
    height: auto;
    margin-left: 0;
    margin-top: 10px;
}

.picture {
    border: 1px solid #CCCCCC;
    padding: 2px
}

.banner_caption {
    font-size: 16px;
    line-height: 15px;
    margin-top: 10px;
    margin-left: 20px;
    margin-bottom: 10px;
    height: 50px;
    width: 95%
}

.caption {
    max-width: 350px;
    font-size: 16px;
    line-height: 14px;
    font-weight: 500;
    float: left;
    clear: both;
    margin-bottom: 10px;
    margin-right: 10px;
}

.thumb-img {
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    float: left;
}

.external_link {
    height: 18px;
    width: 18px;
}

.home_icon {
    height: 30px;
    width: 30px;
}